import React from "react";
import Context from "../../../Context";
import {injectIntl} from "react-intl";
import {SearchOutlined} from "@ant-design/icons";
import {Link} from "react-router-dom";
import {Button, Input, Space, Table, Tag} from "antd";

class OCPPStationsTable extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);

    this.state = {
      searchText: '',
      searchedColumn: '',
      filteredData: [],
      columns: [],
      loading: false,
      pagination: {},
      filters: {},
      sorter: {},
    };
  }

  m = (key, prefix = "stations.table.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  /**
   *    :
   * -     (dataIndex: "identity")
   * -     getValue(record) => string | number | null
   */
  getColumnSearchProps = ({dataIndex, getValue}) => ({
    filterDropdown: ({setSelectedKeys, selectedKeys, confirm, clearFilters}) => (
      <div style={{padding: 8}}>
        <Input
          ref={(node) => {
            this.searchInput = node;
          }}
          placeholder={`Search`}
          value={selectedKeys[0]}
          onChange={(e) => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() =>
            this.handleSearch(selectedKeys, confirm, dataIndex || (getValue && 'custom'))
          }
          style={{marginBottom: 8, display: 'block'}}
        />
        <Space>
          <Button
            type="primary"
            onClick={() =>
              this.handleSearch(selectedKeys, confirm, dataIndex || (getValue && 'custom'))
            }
            icon={<SearchOutlined/>}
            size="small"
            style={{width: 90}}
          >
            Search
          </Button>
          <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{width: 90}}>
            Reset
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered) => (
      <SearchOutlined style={{color: filtered ? '#1890ff' : undefined}}/>
    ),
    onFilter: (value, record) => {
      let fieldValue = null;

      if (getValue) {
        fieldValue = getValue(record);
      } else if (dataIndex) {
        fieldValue = record[dataIndex];
      }

      if (fieldValue === null || fieldValue === undefined) {
        return false;
      }

      return fieldValue.toString().toLowerCase().includes(value.toLowerCase());
    },
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        setTimeout(() => this.searchInput && this.searchInput.select(), 100);
      }
    },
  });

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = (clearFilters) => {
    clearFilters();
    this.setState({searchText: ''});
  };

  setColumns = () => {
    const columns = [
      {
        title: this.m("label"),
        key: "identity",
        dataIndex: "identity",
        width: "25%",
        render: (value, item) => (
          <>
            <Link to={`/ocpp/command/${value}`}>{value}</Link>

            {item.label && item.label !== value && (
              <div className="gx-text-muted gx-font-weight-light">
                {item.label}
              </div>
            )}

            {item.description && item.description !== item.label && (
              <div className="gx-text-muted gx-font-weight-light">
                {item.description}
              </div>
            )}
          </>
        ),
        ...this.getColumnSearchProps({dataIndex: 'identity'}),
        sorter: (a, b) => a.identity.localeCompare(b.identity),
      },
      {
        title: this.m("connection"),
        key: "address",
        dataIndex: "address",
        width: "10%",
        render: (value, item) => (
          <>
            {value}
            {item.connection && (
              <div className="gx-text-muted gx-font-weight-light">
                {item.connection.ip}:{item.connection.port}/{item.connection.socket}
              </div>
            )}
          </>
        ),
      },
      {
        title: this.m("vendor"),
        key: "chargePointVendor",
        dataIndex: "bootNotification",
        width: "10%",
        render: (value, item) => (
          <>
            {value && value.chargePointVendor}
            {item.vendordescription && (
              <div className="gx-text-muted gx-font-weight-light">
                {item.vendordescription}
              </div>
            )}
          </>
        ),
        //   bootNotification.chargePointVendor
        ...this.getColumnSearchProps({
          getValue: (record) =>
            record.bootNotification && record.bootNotification.chargePointVendor,
        }),
        sorter: (a, b) => {
          const av = a.bootNotification?.chargePointVendor || '';
          const bv = b.bootNotification?.chargePointVendor || '';
          return av.localeCompare(bv);
        },
      },
      {
        title: this.m("model"),
        key: "chargePointModel",
        dataIndex: "bootNotification",
        width: "10%",
        render: (value, item) => (
          <>
            {value ? value.chargePointModel : item.modelname}
            {item.modeldescription && (
              <div className="gx-text-muted gx-font-weight-light">
                {item.modeldescription}
              </div>
            )}
          </>
        ),
        //   bootNotification.chargePointModel
        ...this.getColumnSearchProps({
          getValue: (record) =>
            record.bootNotification && record.bootNotification.chargePointModel,
        }),
        sorter: (a, b) => {
          const av = a.bootNotification?.chargePointModel || '';
          const bv = b.bootNotification?.chargePointModel || '';
          return av.localeCompare(bv);
        },
      },
      {
        title: this.m("serialnumber"),
        key: "chargePointSerialNumber",
        dataIndex: "bootNotification",
        align: "center",
        width: "15%",
        render: (value) => (
          <>
            {value && value.chargePointSerialNumber}
            {value && value.meterSerialNumber && value.meterSerialNumber !== value.chargePointSerialNumber && (
              <div className="gx-text-small gx-text-muted">
                {value.meterSerialNumber}
              </div>
            )}
          </>
        ),
        //   bootNotification.chargePointSerialNumber
        ...this.getColumnSearchProps({
          getValue: (record) =>
            record.bootNotification && record.bootNotification.chargePointSerialNumber,
        }),
        filterIcon: (filtered) => (
          <SearchOutlined
            style={{color: filtered ? "#1890ff" : undefined}}
          />
        ),
      },
      {
        title: this.m("connectionupdate"),
        key: "connectionupdate",
        dataIndex: "statusNotification",
        align: "center",
        width: "10%",
        render: (value) => {
          if (value) {
            const dt = new Date(value.timestamp);
            return (
              <div className="gx-text-center">
                {dt.toLocaleDateString()}
                <br/>
                {dt.toLocaleTimeString()}
              </div>
            );
          }
          return null;
        },
      },
      {
        title: this.m("connected"),
        key: "connected",
        dataIndex: "connection",
        width: "10%",
        align: "center",
        sorter: true,
        render: (value) => (
          <div className="gx-text-center">
            <Tag className="gx-m-1" color={value ? "success" : "error"}>
              {value ? this.m("online") : this.m("offline")}
            </Tag>
          </div>
        ),
        filters: [
          {
            text: this.m("online"),
            value: true,
          },
          {
            text: this.m("offline"),
            value: false,
          },
        ],
        onFilter: (value, record) => (record.connection ? true : false) === value,
      },
      {
        title: this.m("statename"),
        key: "statusNotification",
        dataIndex: "statusNotification",
        width: "10%",
        align: "center",
        sorter: true,
        render: (value) => (
          <div className="gx-text-center">
            {value ? (
              <Tag
                className="gx-m-1"
                color={value.status === "Available" ? "success" : "error"}
              >
                {value.status}
              </Tag>
            ) : null}
          </div>
        ),
        filters: [
          {
            text: 'Available',
            value: 'Available',
          },
          {
            text: 'Unavailable',
            value: 'Unavailable',
          },
          {
            text: 'Faulted',
            value: 'Faulted',
          },
        ],
        onFilter: (value, record) =>
          record.statusNotification
            ? record.statusNotification.status.indexOf(value) === 0
            : true,
      }
    ];

    this.setState({columns});
  };

  onMethodFinish = () => {
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  handleTableChange = (pagination, filters, sorter) => {
    this.setState({
      pagination,
      filters,
      sorter,
    });
  };

  render() {
    const {columns, pagination, loading} = this.state;

    return (
      <div>
        <Table
          scroll={{x: 400}}
          showSorterTooltip={false}
          columns={columns}
          rowKey="identity"
          dataSource={this.props.stations}
          pagination={pagination}
          loading={loading || this.props.loading}
          onChange={this.handleTableChange}
        />
      </div>
    );
  }
}

export default injectIntl(OCPPStationsTable);
