import React from "react";
import { Empty, Spin } from "antd";
import { injectIntl } from "react-intl";
import { LoadScript, GoogleMap, Marker, InfoWindow } from "@react-google-maps/api";

const mapContainerStyle = {
  width: "100%",
  height: "100%",
  minHeight: "500px",
};

const defaultCenter = {
  lat: 55.7558,
  lng: 37.6173,
};

const defaultZoom = 9;

class StationsMapGoogle extends React.Component {
  constructor(props) {
    super(props);

    const appConfig = window.appConfig || {};

    if (!process.env.REACT_APP_GOOGLE_MAPS_API_KEY) {
      console.error(' Google Maps API key is missing in environment variables');
    }

    const storedLanguage = localStorage.getItem("ocppcsDefaultLanguage");
    const defaultLanguage = appConfig.mapLanguage || appConfig.defaultLanguage;
    let loadLanguage = storedLanguage ? storedLanguage : defaultLanguage;

    this.config = {
      ...appConfig,
      googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
      mapLanguage: loadLanguage
    };

    this.state = {
      isLoaded: false,
      loadError: null,
      selectedStation: null,
      googleMapsReady: false,
      currentLanguage: loadLanguage
    };
  }

  componentDidMount() {
    this.checkLanguageChange();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.handleLanguageChange();
    }
  }

  //     
  checkLanguageChange = () => {
    const currentLanguage = localStorage.getItem("ocppcsDefaultLanguage");
    if (currentLanguage && currentLanguage !== this.state.currentLanguage) {
      this.setState({ currentLanguage });
    }
  };

  //   
  handleLanguageChange = () => {
    const storedLanguage = localStorage.getItem("ocppcsDefaultLanguage");
    const appConfig = window.appConfig || {};
    const defaultLanguage = appConfig.mapLanguage || appConfig.defaultLanguage;
    const newLanguage = storedLanguage ? storedLanguage : defaultLanguage;

    if (newLanguage !== this.state.currentLanguage) {
      this.setState({
        currentLanguage: newLanguage,
        googleMapsReady: false,
        isLoaded: false
      });

      this.config.mapLanguage = newLanguage;
    }
  };

  componentWillUnmount() {
    if (this.languageCheckInterval) {
      clearInterval(this.languageCheckInterval);
    }
  }

  m = (key, prefix = "stations.map.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  getItemGeo = (item) => {
    if (!item?.geo?.[0]) return null;

    const geo = item.geo[0];
    const lat = parseFloat(geo.latitude);
    const lng = parseFloat(geo.longitude);

    if (isNaN(lat) || isNaN(lng)) return null;

    return { lat, lng };
  };

  handleScriptLoad = () => {
    this.setState({ googleMapsReady: true });
  };

  handleScriptError = (error) => {
    console.error(' Google Maps script error:', error);
    this.setState({
      googleMapsReady: true,
      loadError: error
    });
  };

  handleMapLoad = (map) => {
    this.setState({ isLoaded: true });

    if (window.google && window.google.maps) {
      try {
        const mapOptions = {
          zoomControl: true,
          streetViewControl: false,
          mapTypeControl: false,
          fullscreenControl: true,
          language: this.state.currentLanguage,
          region: this.getRegionFromLanguage(this.state.currentLanguage)
        };

        map.setOptions(mapOptions);
      } catch (error) {
        console.warn('Could not update map language options:', error);
      }
    }

    setTimeout(() => {
      this.fitMapToMarkers(map);
    }, 300);
  };

  //       
  getRegionFromLanguage = (language) => {
    const regionMap = {
      'ru': 'RU',
      'en': 'US',
      'de': 'DE',
      'fr': 'FR',
      'es': 'ES',
      'zh': 'CN',
      'ja': 'JP',
      'ko': 'KR'
    };
    return regionMap[language] || language.toUpperCase();
  };

  handleMapError = (error) => {
    console.error(' Google Map error:', error);
    this.setState({ isLoaded: true, loadError: error });
  };

  fitMapToMarkers = (map) => {
    if (!map) return;

    const { stations } = this.props;
    const points = stations
      .map((item) => this.getItemGeo(item))
      .filter((geo) => geo);

    if (points.length === 0) return;

    try {
      if (points.length === 1) {
        map.setCenter(points[0]);
        map.setZoom(12);
      } else if (points.length > 1) {
        if (window.google?.maps?.LatLngBounds) {
          const bounds = new window.google.maps.LatLngBounds();
          points.forEach(point => bounds.extend(point));
          map.fitBounds(bounds);
        } else {
          console.warn('Google Maps API not fully loaded, retrying...');
          setTimeout(() => this.fitMapToMarkers(map), 100);
        }
      }
    } catch (error) {
      console.error('Error fitting bounds:', error);
    }
  };

  handleMarkerClick = (station) => {
    this.setState({ selectedStation: station });
  };

  handleInfoWindowClose = () => {
    this.setState({ selectedStation: null });
  };

  renderMapContent() {
    const { stations } = this.props;
    const { selectedStation } = this.state;

    if (!this.state.googleMapsReady) {
      return null;
    }

    return (
      <>
        {stations.map((item, idx) => {
          const geo = this.getItemGeo(item);
          if (!geo) return null;

          return (
            <Marker
              key={idx}
              position={geo}
              onClick={() => this.handleMarkerClick(item)}
              options={{
                icon: {
                  url: item.connected || item.connection ? "/assets/images/flash-green.png" : "/assets/images/flash-red.png",
                  scaledSize: { width: 32, height: 32 },
                }
              }}
            />
          );
        })}

        {selectedStation && (
          <InfoWindow
            position={this.getItemGeo(selectedStation)}
            onCloseClick={this.handleInfoWindowClose}
          >
            <div style={{ maxWidth: "250px" }}>
              <h4 style={{ margin: "0 0 8px 0" }}>{selectedStation.label}</h4>
              {selectedStation.description && (
                <div style={{ marginBottom: "6px", fontSize: "13px" }}>
                  {selectedStation.description}
                </div>
              )}
              {selectedStation.modelname && (
                <div style={{ marginBottom: "6px", fontSize: "13px" }}>
                  {selectedStation.modelname} {selectedStation.vendorname || ""}
                </div>
              )}
              <a
                href={this.props.ocpp ? `/ocpp/command/${selectedStation.identity}` : `/crm/station/${selectedStation.id}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{ color: "#1890ff", fontSize: "13px" }}
              >
                {this.props.ocpp ? this.m("select") : this.m("more")}
              </a>
            </div>
          </InfoWindow>
        )}
      </>
    );
  }

  render() {
    const { stations, className } = this.props;
    const { isLoaded, loadError, googleMapsReady, currentLanguage } = this.state;

    if (!Array.isArray(stations)) {
      console.warn('Stations is not an array');
      return null;
    }

    if (stations.length === 0) {
      return <Empty description="There are no stations to display" />;
    }

    const points = stations
      .map((item) => this.getItemGeo(item))
      .filter((geo) => geo);

    if (points.length === 0) {
      return (
        <div className={className}>
          <Empty description="No coordinates for stations" />
        </div>
      );
    }

    const center = points.length > 0 ? points[0] : defaultCenter;

    if (loadError) {
      return (
        <div className={className} style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          height: '400px',
          flexDirection: 'column',
          padding: '20px'
        }}>
          <h3 style={{ color: '#ff4d4f' }}>  </h3>
          <p>{loadError.toString()}</p>
        </div>
      );
    }

    if (!process.env.REACT_APP_GOOGLE_MAPS_API_KEY) {
      return (
        <div className={className} style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          height: '400px',
          flexDirection: 'column'
        }}>
          <h3 style={{ color: '#ff4d4f' }}>  API </h3>
          <p> googleMapsApiKey  env</p>
        </div>
      );
    }

    return (
      <div className={className}>
        {(!isLoaded || !googleMapsReady) && (
          <div style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            zIndex: 1
          }}>
            <Spin size="large" tip="Loading map..." />
          </div>
        )}

        <LoadScript
          googleMapsApiKey={this.config.googleMapsApiKey}
          language={currentLanguage}
          region={this.getRegionFromLanguage(currentLanguage)}
          loadingElement={<div style={{ height: '100%', width: '100%', background: '#f0f0f0' }} />}
          onLoad={this.handleScriptLoad}
          onError={this.handleScriptError}
        >
          <GoogleMap
            mapContainerStyle={mapContainerStyle}
            center={center}
            zoom={defaultZoom}
            onLoad={this.handleMapLoad}
            onError={this.handleMapError}
            options={{
              zoomControl: true,
              streetViewControl: false,
              mapTypeControl: false,
              fullscreenControl: true,
              language: currentLanguage,
            }}
          >
            {this.renderMapContent()}
          </GoogleMap>
        </LoadScript>
      </div>
    );
  }
}

export default injectIntl(StationsMapGoogle);
