import {Card, Empty, Tag} from "antd";
import React from "react";
import {injectIntl} from "react-intl";
import {Row, Col} from "antd";
import Flex from "../../../util/Flex";
import StationIcon from "../../Stations/StationIcon";
import {Link} from "react-router-dom";

class OCPPStationsTiles extends React.Component {

  m = (key, prefix = "stations.tiles.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  render() {
    const {stations} = this.props;

    if (!Array.isArray(stations)) return null;
    if (stations.length === 0) return <Empty/>;
    return (
      <>
        <Row gutter={[16, 16]}>
          {stations.map((item, index) => {
            return (
              <Col key={item.identity} xl={8} lg={8} sm={12} xs={24}>
                <Card className="gx-mb-0 gx-h100">
                  <Flex column between alignItems="start" className="gx-h100">
                    <div>
                      <Link to={`/ocpp/command/${item.identity}`} className="gx-link">
                        <Flex
                          between
                          className="gx-w100 gx-pt-2 gx-mt-2"
                        >
                          <StationIcon {...item} />
                          <div className="gx-ml-2 gx-mr-4">
                            <div>{item.identity}</div>
                            {item.label && item.identity !== item.label ? (
                              <div className="gx-text-small">
                                {item.label}
                              </div>
                            ) : null}

                            {item.description && (
                              <div className="gx-font-weight-light gx-small gx-text-muted">
                                {item.description}
                              </div>
                            )}
                          </div>
                        </Flex>
                      </Link>
                    </div>

                    <Flex
                      between
                      className="gx-w100 gx-pt-2 gx-mt-2"
                    >
                      {item.bootNotification && (
                        <>
                          <div className="gx-mr-4">{item.bootNotification.chargePointVendor}</div>
                          <div>{item.bootNotification.chargePointSerialNumber}</div>
                        </>
                      )}
                    </Flex>

                    <Flex
                      between
                      className="gx-w100 gx-pt-2"
                    >
                      {item.bootNotification && (
                        <>
                          <div className="gx-mr-4">{item.bootNotification.chargePointModel}</div>
                          <div>{item.bootNotification.firmwareVersion}</div>
                        </>
                      )}
                    </Flex>

                    <Flex
                      between
                      className="gx-w100 gx-pt-2 gx-mt-2"
                    >
                      <div className="gx-mr-4">{item.address}</div>

                      {item.connection && (
                        <div className="gx-text-muted gx-font-weight-right">
                          {item.connection.ip}:{item.connection.port}/{item.connection.socket}
                        </div>
                      )}
                    </Flex>

                    <Flex
                      between
                      className="gx-w100 gx-border-top gx-pt-3 gx-mt-2"
                    >
                      <div className="gx-mr-4">{item.typename}</div>
                      <Flex>
                        <Tag className="gx-m-1" color={item.connection ? "success" : "error"}>
                          {item.connection ? this.m("online") : this.m("offline")}
                        </Tag>

                        {item.statusNotification &&
                          <Tag className="gx-m-1"
                               color={item.statusNotification.status === "Available" ? "success" : "error"}>
                            {item.statusNotification.status}
                          </Tag>
                        }
                      </Flex>
                    </Flex>
                  </Flex>
                </Card>
              </Col>
            );
          })}
        </Row>
      </>
    );
  }
}

export default injectIntl(OCPPStationsTiles);
