import React from "react";
import Context from "../../Context";
import {withRouter} from "react-router-dom";
import {injectIntl} from "react-intl";
import StationsMapYandex from "./StationsMapYandex";
import StationsMapGoogle from "./StationsMapGoogle";

class StationsMapProvider extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);

    const appConfig = window.appConfig || {};

    this.config = {
      map: appConfig.map,
    };
  }

  render() {
    const { className, stations, ocpp, showMoreButton, onMethodFinish } = this.props;

    return (
      <>
        {this.config.map === "yandex" ? (
          <StationsMapYandex
            key="id"
            stations={stations}
            className={className}
            showMoreButton={showMoreButton}
            ocpp={ocpp}
            onMethodFinish={onMethodFinish}
          />
        ) : (
          <StationsMapGoogle
            key="id"
            stations={stations}
            className={className}
            showMoreButton={showMoreButton}
            ocpp={ocpp}
            onMethodFinish={onMethodFinish}
          />
        )}
      </>
    );
  }
}

export default withRouter(injectIntl(StationsMapProvider));
