import React from "react";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import { Spin, Tabs} from "antd";
import {AppstoreOutlined, InsertRowAboveOutlined} from "@ant-design/icons";
import Title from "../../Admin/Title";
import visualState from "../../../util/visualState";
import OCPPStationsTable from "./OCPPStationsTable";
import OCPPStationsTiles from "./OCPPStationsTiles";
import StationsMapProvider from "../../Stations/StationsMapProvider";

const vState = new visualState("Stations");

class OCPPStations extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);

    const appConfig = window.appConfig || {};

    this.config = {
      map: appConfig.map,
    };

    this.state = {
      pending: false,
      stations: [],
      mode: vState.read("mode", "tiles"),
    };
  }

  m = (key, prefix = "ocpp.stationlist.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  load = async () => {
    this.setState({ pending: true });

    let stations = this.context.ocpp.getArray(
      await this.context.ocpp.q("/ChargePointList", {}),
      "ChargePointList",
      "identity"
    );

    this.setState({ pending: false, stations });
  };

  onMethodFinish = () => {
    this.load();
  };

   render() {
    const { pending, stations, mode } = this.state;

    return (
      <Spin spinning={pending}>
        <Title
          title={this.m("stationlist")}
          url="/ocpp"
          icon={<AppstoreOutlined />}
        />

        <Tabs
          defaultActiveKey={mode}
          onChange={this.modeSwitch}
          className="gx-mb-4 gx-h-100"
        >
          <Tabs.TabPane
            tab={<AppstoreOutlined title={this.m("as.tiles")}/>}
            key="tiles"
          >
            <Spin spinning={pending}>
              <OCPPStationsTiles
                key="id"
                stations={stations}
                onMethodFinish={this.onMethodFinish}
              />
            </Spin>
          </Tabs.TabPane>

          <Tabs.TabPane
            tab={
              <div
                className="icon icon-map-simple"
                title={this.m("as.map")}
              ></div>
            }
            key="map"
            className="gx-h-100"
          >
            <StationsMapProvider
              stations={stations}
              className="gx-st-map big"
              ocpp={true}
              onMethodFinish={this.onMethodFinish}
            />
          </Tabs.TabPane>

          <Tabs.TabPane
            tab={<InsertRowAboveOutlined title={this.m("as.table")}/>}
            key="table"
            className="gx-h-100"
          >
            <OCPPStationsTable
              key="id"
              stations={stations}
              onMethodFinish={this.onMethodFinish}
            />
          </Tabs.TabPane>
        </Tabs>
      </Spin>
    );
  }
}

export default injectIntl(OCPPStations);
