import React from "react";
import { Empty, Spin } from "antd";
import { injectIntl } from "react-intl";
import { YMaps, Map, Placemark, ZoomControl } from "react-yandex-maps";

class StationsMapYandex extends React.Component {
  constructor(props) {
    super(props);

    const appConfig = window.appConfig || {};

    if (!process.env.REACT_APP_YANDEX_MAPS_API_KEY) {
      console.error(' Yandex Maps API key is missing in environment variables');
    }

    this.config = {
      ...appConfig,
      yandexMapsApiKey: process.env.REACT_APP_YANDEX_MAPS_API_KEY,
      mapLanguage: this.getMapLanguage(appConfig)
    };

    this.state = {
      mapLoaded: false,
    };
  }

  m = (key, prefix = "stations.map.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  getYandexLanguage = (language) => {
    const codeMap = {
      'ru': 'ru_RU',
      'en': 'en_US',
      'de': 'de_DE',
      'fr': 'fr_FR',
      'it': 'it_IT',
    };

    return codeMap[language] || language.toUpperCase();
  };

  getMapLanguage = (appConfig) => {
    const storedLanguage = localStorage.getItem("ocppcsDefaultLanguage");
    const defaultLanguage = appConfig.mapLanguage || appConfig.defaultLanguage;
    return this.getYandexLanguage(storedLanguage ? storedLanguage : defaultLanguage);
  }

  getItemGeo = (item) => {
    let geo = item?.geo?.[0] || {};
    let lat = geo.latitude;
    let lng = geo.longitude;
    if (lat && lng) {
      return [lat, lng];
    }
    return null;
  };

  render() {
    const { stations } = this.props;
    if (!Array.isArray(stations)) return null;
    if (stations.length === 0) return <Empty />;

    const mapRef = React.createRef();

    const points = stations
      .map((item) => this.getItemGeo(item))
      .filter((geo) => geo);

    if (points.length === 0) {
      return (
        <div className={this.props.className}>
          <Empty />
        </div>
      );
    }

    const lang = this.getMapLanguage(this.config);
    const apikey = this.config.yandexMapsApiKey;

    return (
      <div className={this.props.className}>
        {!this.state.mapLoaded && <Spin spinning></Spin>}
        <YMaps key={lang} query={ {apikey, lang} } >
          <Map
            defaultState={{ center: points[0] || [], zoom: 9 }}
            width={"100%"}
            height={"100%"}
            modules={["geoObject.addon.balloon", "util.bounds"]}
            onLoad={(maps) => {
              this.setState({ mapLoaded: true });
              if (points.length > 1) {
                mapRef.current.setBounds(maps.util.bounds.fromPoints(points));
              }
            }}
            instanceRef={(ref) => {
              if (ref) {
                mapRef.current = ref;
              }
            }}
          >
            {stations.map((item, idx) => {
              let geo = this.getItemGeo(item);
              if (!geo) return null;

              const balloonContentFooter = this.props.ocpp ? `<a href="/ocpp/command/${item.identity}">${this.m("select")}</a>` : `<a href="/crm/station/${item.id}">${this.m("more")}</a>`;

              let balloonContent = [];
              if (item.description)
                balloonContent.push(`<div>${item.description || ""}</div>`);
              if (item.modelname)
                balloonContent.push(
                  `<div>${item.modelname || ""} ${item.vendorname || ""}</div>`
                );

              return (
                <Placemark
                  key={idx}
                  defaultGeometry={geo}
                  options={{
                    iconLayout: "default#image",
                    iconImageHref: item.connected || item.connection ? "/assets/images/flash-green.png" : "/assets/images/flash-red.png",
                    iconImageSize: [32, 32],
                  }}
                  properties={{
                    balloonContentHeader: `${item.label}`,
                    balloonContent: balloonContent.join(""),
                    balloonContentFooter: balloonContentFooter,
                  }}
                />
              );
            })}
            <ZoomControl />
          </Map>
        </YMaps>
      </div>
    );
  }
}
export default injectIntl(StationsMapYandex);
